    height: 100vh;
    width: 100%;
    background: rgb(238,174,202);
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
}

#content {
    min-height: 60%;
    max-height: 60%;
    width: 60%;
    border-radius: 1em;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 1px -3px 65px 0px rgba(0,0,0,0.83);
    -webkit-box-shadow: 10px 15px 65px 0px rgba(0,0,0,0.83);
    -moz-box-shadow: 10px 15px 65px 0px rgba(0,0,0,0.83);
}

#sidebar {
    height: 100%;
    width: 25%;
    background: rgba(255, 255, 255, 0.411);
    border-radius: 1rem 0 0 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 2em;
}

#taskList {
    height: 100%;
    width: 75%;
    background: rgba(255, 255, 255, 0.199);
    border-radius: 0 1rem 1rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    overflow: auto;
}

footer {
    width: 100%;
    padding: 0.5em 0;
    margin: 0;
    position: absolute;
    bottom: 0;
    font-size: 1em;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    background: rgba(155, 218, 199, 0.274);
    text-align: center;
}

#title {
    text-align: center;
    margin: 0 0 2rem 0;
    padding: 0;
    font-size: 3em;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

a, a:link, a:visited {
    color: black;
    text-decoration: none;
}

#addTaskBtn {
    margin: 1em 4em;
    position: absolute;
    top: 25%;
    right: 20%;
    padding: 1em 1.5em;
    border-radius: 50%;
    border: none;
    box-shadow: 10px 10px 17px 3px rgba(0,0,0,0.3);
    -webkit-box-shadow: 10px 10px 17px 3px rgba(0,0,0,0.3);
    -moz-box-shadow: 10px 10px 17px 3px rgba(0,0,0,0.3);
    font-size: large;
    z-index: 1;
}

#addTaskBtn:hover {
    background-color: rgb(255, 215, 213);
}

.modal {
    display: none;
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.4);
}
.modal-content {
    background-color: #fefefe;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}
.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

#taskDesc {
    width: 100%;
    height: 20rem;
    resize: none;
}

.submitButton:hover {
    background-color: rgba(206, 255, 248, 0.288);
}

.task {
    width: 100%;
    background-color: rgba(240, 248, 255, 0.603);
    margin-bottom: 0.5em;
    word-wrap: break-word;
    white-space: normal;
}

#taskList > div:first-of-type {
    margin-top: 6rem;
}

#all, #today, #importantTasks,#completed {
    width: 100%;
    border: none;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: x-large;
}

#all {
    border-radius: 1rem 0 0 0;
}`, "",{"version":3,"sources":["webpack://./src/main.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,WAAW;IACX,4BAA4B;IAC5B,qFAAqF;IACrF,UAAU;IACV,SAAS;IACT,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;;AAE3B;;AAEA;IACI,eAAe;IACf,eAAe;IACf,UAAU;IACV,kBAAkB;IAClB,SAAS;IACT,UAAU;IACV,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,8CAA8C;IAC9C,uDAAuD;IACvD,oDAAoD;AACxD;;AAEA;IACI,YAAY;IACZ,UAAU;IACV,sCAAsC;IACtC,4BAA4B;IAC5B,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,2BAA2B;IAC3B,QAAQ;AACZ;;AAEA;IACI,YAAY;IACZ,UAAU;IACV,sCAAsC;IACtC,4BAA4B;IAC5B,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,2BAA2B;IAC3B,kBAAkB;IAClB,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,SAAS;IACT,kBAAkB;IAClB,SAAS;IACT,cAAc;IACd,qEAAqE;IACrE,sCAAsC;IACtC,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,kBAAkB;IAClB,UAAU;IACV,cAAc;IACd,qEAAqE;AACzE;;AAEA;IACI,YAAY;IACZ,qBAAqB;AACzB;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,QAAQ;IACR,UAAU;IACV,kBAAkB;IAClB,kBAAkB;IAClB,YAAY;IACZ,8CAA8C;IAC9C,sDAAsD;IACtD,mDAAmD;IACnD,gBAAgB;IAChB,UAAU;AACd;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,aAAa;IACb,eAAe;IACf,WAAW;IACX,OAAO;IACP,MAAM;IACN,WAAW;IACX,YAAY;IACZ,cAAc;IACd,iCAAiC;AACrC;AACA;IACI,yBAAyB;IACzB,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,UAAU;IACV,qEAAqE;AACzE;AACA;IACI,WAAW;IACX,YAAY;IACZ,eAAe;IACf,iBAAiB;AACrB;AACA;;IAEI,YAAY;IACZ,qBAAqB;IACrB,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,4CAA4C;AAChD;;AAEA;IACI,WAAW;IACX,4CAA4C;IAC5C,oBAAoB;IACpB,qBAAqB;IACrB,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,qEAAqE;IACrE,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;AAC7B","sourcesContent":["body {\n    height: 100vh;\n    width: 100%;\n    background: rgb(238,174,202);\n    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);\n    padding: 0;\n    margin: 0;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    \n}\n\n#content {\n    min-height: 60%;\n    max-height: 60%;\n    width: 60%;\n    border-radius: 1em;\n    margin: 0;\n    padding: 0;\n